const dailyProgress = {    
    //Progress returned in percentage
    todaysProgress: () => { 
        const ONE_DAY = 86400000;
        let today = new Date(); 
        let firstDayThisYear = new Date(today.getFullYear(), 0, 1); 
        let progress = Math.ceil( ((today - firstDayThisYear + 1) / ONE_DAY ) / 365 * 100); 
        return progress; 
    },
    fromThisDaysProgress: (thisDate) => {
        const ONE_DAY = 86400000;
        thisDate = new Date(thisDate);
        let firstDayThisYear = new Date(thisDate.getFullYear(), 0, 1); 
        let progress = Math.ceil( ((thisDate - firstDayThisYear + 1) / ONE_DAY ) / 365 * 100); 
        return progress;
    },
}
module.exports = dailyProgress;


